 <template>
  <div class="container-fluid">
    <div class="row">
      <h1 class="display-4">Welcome to Taco Loco</h1>
    </div>
    <h4>Main Menu</h4>
    <hr>
    <div class="row">
      <div class="col-md-9">
        <div class="card-deck">
          <div
            name="menuItem"
            class="card"
            style="max-width: 18rem; max-height: 20rem;"
            v-for="menuItem in menuItemsList"
          >
            <a
              href="#"
              class="list-group-item list-group-item-action"
              @click="addItemOrder(menuItem)"
              data-toggle="tooltip"
              title="Hooray!"
            >
              <img class="card-img-top" :src="menuItem.itemImgURL" alt="Card image cap">
              <div class="card-body">
                <h6 class="card-title">{{menuItem.itemId}} - {{menuItem.itemName}}</h6>
                <p>{{menuItem.itemDescription}}</p>
                <hr>
                <p>price: $ {{parseFloat(menuItem.itemPrice).toFixed(2)}}</p>
              </div>
            </a>
          </div>
        </div>
      </div>

      <div class="col-md-3">
        <div class="card">
          <div>
            <h5 class="card-header">Shopping Cart</h5>

            <div>
              <ul class="list-group list-group-flush">
                <li class="list-group-item" v-for="order in orderItemsList.orderList">
                  <a
                    href="#"
                    class="list-group-item list-group-item-action"
                    @click="removeItem(order)"
                  >
                    <img :src="order.itemImgURL" height="50" width="50">
                    {{order.itemId}} # {{order.itemName}} $ {{parseFloat(order.itemPrice).toFixed(2)}}
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <hr>
          <div>
            <p>Sub Total: {{'$'}} {{ parseFloat(orderItemsList.totalOrderPrice).toFixed(2) }}</p>
            <P>Discount: $ {{parseFloat(orderItemsList.discountTotal).toFixed(2)}}</P>
            <p>Total: $ {{parseFloat(orderItemsList.discountedOrderPrice).toFixed(2)}}</p>
            <p>Number of Items: {{orderItemsList.orderSize}}</p>
          </div>
          <br>
          <div>
            <button class="card-link btn btn-sm btn-primary" @click="payOrder">Pay Order</button>
            <button class="card-link btn btn-sm btn-primary" @click="cancelOrder">Cancel Order</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

    <script>
import axios from "axios";

export default {
 
  data: function() {
    return { menuItemsList: [], orderItemsList: [] };
  },

  mounted() {
    this.cancelOrder();
    axios({ method: "GET", url: "http://67.149.229.181/menu" }).then(
      response => {
        this.menuItemsList = response.data;
        console.log(this.menuItemsList);
      },
      error => {
        console.error(error);
      }
    );
  },
  methods: {
    addItemOrder(menuItem) {
      axios
        .post("http://67.149.229.181/addItemOrder", menuItem)
        .then(response => {
          this.orderItemsList = response.data;
          console.log(this.orderItemsList);
        })
        .catch(e => {
          console.error(e);
        });
    },
    cancelOrder() {
      axios
        .post("http://67.149.229.181/cancelOrder")
        .then(response => {
          this.orderItemsList = response.data;
          console.log(this.orderItemsList);
        })
        .catch(e => {
          console.error(e);
        });
    },
    removeItem(menuItem) {
      axios
        .post("http://67.149.229.181/removeItemOrder", menuItem)
        .then(response => {
          this.orderItemsList = response.data;
          console.log(this.orderItemsList);
        })
        .catch(e => {
          console.error(e);
        });
    },
    payOrder() {
      alert("Thanks for Shopping with us, see you soon !!!");
      this.cancelOrder();
    }
  }
};
</script>
    


